@import url("../assets/font/Inter/stylesheet.css");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
* ::-webkit-scrollbar {
  width: 6px;
  border-radius: 5px;
  height: 100%;
}
* ::-webkit-scrollbar-thumb {
  background: #ddd;
  border-radius: 16px;
}
* :hover::-webkit-scrollbar-thumb {
  background: #b9b9b9;
  border-radius: 10px;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}

body {
  outline: 0;
  color: #000;
  font-size: 14px;
  line-height: 1.5;
  background: #0a192f;
  font-family: "Inter", sans-serif;
}

a {
  text-decoration: none;
  color: #000;
}
a::after {
  content: attr(title);
  display: block;
  font-weight: 700;
  height: 0;
  overflow: hidden;
  visibility: hidden;
}

button,
select {
  border: none;
  outline: none;
}

img {
  width: 100%;
  display: block;
  max-width: 100%;
  object-fit: cover;
}

.container {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  max-width: 120rem;
  padding: 0 2rem;
  position: relative;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu {
  display: block;
}

ol,
ul {
  list-style: none !important;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.btn-contact {
  display: block;
  font-weight: 600;
  width: fit-content;
  color: #64ffda;
  font-size: 1.4rem;
  position: relative;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #64ffda;
  transition: 0.4s ease-in-out;
}
@media screen and (min-width: 1201px) {
  .btn:hover {
    box-shadow: 0.4rem 0.4rem 0 0 #64ffda;
    transform: translateY(-0.2rem) translateX(-0.2rem);
  }
}

.banner {
  z-index: 3;
  position: relative;
  height: calc(100vh - 9.2rem);
}
.banner-position {
  height: 100%;
  padding-inline: 15rem;
}
.banner-content {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header {
  padding: 2rem;
}
.header-wrap {
  height: 100%;
}
.header-menu {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}
.header-logo a {
  color: white;
  display: block;
  position: relative;
}
.header-logo a::before {
  content: "";
  bottom: 0.8rem;
  right: -1.4rem;
  background: #64ffda;
  position: absolute;
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 999px;
}
.header-logo a h1 {
  color: inherit;
  line-height: normal;
  font-size: 3.4rem;
}
.header-row {
  gap: 2rem;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.header-list .list {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.header-list .list-item {
  color: white;
  padding: 1.5rem;
}
.header-list .list-item a {
  color: inherit;
  font-weight: 500;
  font-size: 1.4rem;
  transition: 0.4s all;
}
@media screen and (min-width: 1201px) {
  .header-list .list-item a:hover {
    color: #64ffda;
  }
}
.header-list .list-item a span {
  margin-right: 0.5rem;
  color: #64ffda;
}

@media screen and (max-width: 1024px) {
  .main {
    height: auto;
  }
}
@media screen and (max-width: 767px) {
  .main {
    height: 100vh;
  }
}

.cv {
  padding: 2rem;
  cursor: default;
  overflow: hidden;
  flex-direction: column;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}
@media screen and (max-width: 767px) {
  .cv {
    height: 100%;
  }
}
.cv .container {
  max-width: 95rem;
}
.cv-wrap {
  height: 100%;
  padding: 3rem;
  background: #fff;
  position: relative;
  flex-direction: column;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}
@media screen and (max-width: 767px) {
  .cv-wrap {
    display: none;
  }
}
.cv-information {
  gap: 0.5rem;
  position: relative;
  text-align: center;
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  flex-direction: column;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}
.cv-information::before {
  content: "";
  bottom: 0;
  left: -3rem;
  width: calc(100% + 6rem);
  position: absolute;
  border-bottom: 0.1rem solid #e5e5e5;
}
.cv-information .fullname {
  font-size: 2.6rem;
  text-transform: uppercase;
}
.cv-information .position {
  font-weight: 600;
  text-transform: uppercase;
}
.cv-information .box {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
  justify-content: center;
  -webkit-justify-content: center;
}
.cv-information .box a:nth-child(1)::after {
  content: "|";
  margin: 0 0.2rem;
  display: initial;
  font-weight: 300;
  visibility: visible;
}
.cv-information a {
  position: relative;
  font-size: 1.6rem;
}
@media screen and (min-width: 1201px) {
  .cv-information a:hover span {
    color: #f00;
  }
}
.cv-information .social {
  display: block;
}
.cv-education {
  padding-bottom: 2rem;
}
.cv-education-school {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}
.cv-education-school h3 {
  text-transform: uppercase;
}
.cv-experience {
  padding-bottom: 2rem;
}
.cv-experience-info li span {
  text-transform: uppercase;
}
.cv-experience-information li span {
  font-weight: 600;
  width: fit-content;
  display: inline-block;
  text-decoration: underline;
}
.cv-company:not(:last-child) {
  margin-bottom: 2rem;
}
.cv-company-box {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
  justify-content: space-between;
  -webkit-justify-content: space-between;
}
.cv-company-box .company {
  text-transform: uppercase;
}
.cv-skill p span {
  font-weight: 700;
}
.cv-skill-link {
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
.cv-skill-link p {
  margin-right: 0.4rem;
}
@media screen and (min-width: 1201px) {
  .cv-skill-link a:hover {
    color: #f00;
  }
}
.cv-mobile {
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
  flex-direction: column;
}
@media screen and (max-width: 767px) {
  .cv-mobile {
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    -ms-align-items: center;
    align-items: center;
    -webkit-align-items: center;
    justify-content: center;
    -webkit-justify-content: center;
  }
}
.cv-mobile-block {
  gap: 2rem;
  padding: 2rem;
  background: #fff;
  border-radius: 1.7rem;
  flex-direction: column;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
}
.cv-mobile h2 {
  line-height: 1.8;
  font-size: 1.8rem;
  text-align: center;
}
.cv-mobile a {
  margin: 0 auto;
  width: fit-content;
  background: #fff;
  padding: 1rem 3rem;
  border-radius: 0.5rem;
  border: 0.1rem solid #f00;
}
.cv .title {
  font-size: 1.8rem;
  text-align: center;
  margin-bottom: 1rem;
  text-transform: uppercase;
}
.cv .dot {
  padding-left: 3rem;
  position: relative;
}
.cv .dot::before {
  content: "";
  top: 0.74rem;
  left: 1.5rem;
  width: 0.6rem;
  height: 0.6rem;
  border-radius: 50%;
  background: #000;
  position: absolute;
}
.cv-back {
  width: fit-content;
  margin: 0 0 0 auto;
}
@media screen and (max-width: 767px) {
  .cv-back {
    display: none;
  }
}
.cv-back .btn-back {
  margin-top: 2rem;
  justify-content: flex-end;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  -ms-align-items: center;
  align-items: center;
  -webkit-align-items: center;
}
@media screen and (min-width: 1201px) {
  .cv-back .btn-back:hover img {
    filter: invert(75%) sepia(81%) saturate(283%) hue-rotate(104deg) brightness(102%) contrast(101%);
    transform: translateX(0.8rem);
  }
  .cv-back .btn-back:hover span {
    color: #64ffda;
  }
}
.cv-back img {
  filter: brightness(0) invert(1);
  transition: 0.4s all;
  width: 1.6rem;
  height: 1.6rem;
}
.cv-back span {
  color: white;
  display: block;
  margin-left: 1rem;
  font-size: 1.6rem;
  transition: 0.4s all;
}

.home {
  color: white;
}

.person-welcome .hello {
  font-size: 2rem;
  font-weight: 600;
  color: #64ffda;
  margin-bottom: 2rem;
}
.person-introduce .content {
  color: #8892b0;
  font-size: 2rem;
}
.person-contact {
  margin-top: 2rem;
}
.person .heading {
  font-size: 6rem;
  font-weight: 600;
  line-height: 1.5;
}
.person .heading.name {
  color: white;
}
.person .heading.work {
  color: #8892b0;
}